import { useEffect, useState } from 'react'
import { Container, Nav, Navbar } from 'react-bootstrap'
import logo from '../assets/img/LogoSVG.svg'
import navIcon1 from '../assets/img/nav-icon1.svg'
import navIcon2 from '../assets/img/nav-icon2.svg'
import "../App.css";
import { Link } from 'react-router-dom';

export const NavBar = () => {
    const [activeLink, setActiveLink] = useState('home');
    const [scrolled, setScrolled] = useState(false);

    useEffect(() => {
        const onScroll = () => {
            setScrolled(window.scrollY > 50);
        };

        window.addEventListener('scroll', onScroll);
        return () => window.removeEventListener('scroll', onScroll);
    }, []);

    const onUpdateActiveLink = (value) => {
        setActiveLink(value);
    };

    const handleScrollToSection = (sectionId) => {
        const section = document.getElementById(sectionId);
        if (section) {
            section.scrollIntoView({ behavior: 'smooth' });
        }
    };

    return (
        <Navbar expand="lg" className={scrolled ? "scrolled" : ""}>
            <Container >
                <Navbar.Brand as={Link} to="/">
                    <img src={logo} alt="logo" />
                </Navbar.Brand>
                
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="me-auto">
                    <Nav.Link
                        as={Link}
                        to="/"
                        className={activeLink === 'home' ? 'active navbar-link' : 'navbar-link'}
                        onClick={() => {
                            onUpdateActiveLink('home');
                            handleScrollToSection('home');
                        }}
                    >
                        Home
                    </Nav.Link>

                        <Nav.Link
                            as={Link}
                            to="/"
                            className={activeLink === 'projects' ? 'active navbar-link' : 'navbar-link'}
                            onClick={() => {
                                onUpdateActiveLink('projects');
                                handleScrollToSection('projects');
                            }}
                        >
                            Projects
                        </Nav.Link>
                    </Nav>
                    <span className='navbar-text'>
                        <div className='social-icon'>
                            <a href="https://www.linkedin.com/in/kael-anderson6/" aria-label='Linkedin' rel='noopener' target='_blank'>
                                <img src={navIcon1} alt="" />
                            </a>

                            <a href="https://github.com/KaelAnderson" aria-label='GitHub' rel='noopener' target='_blank'>
                                <img src={navIcon2} alt="" />
                            </a>

                            
                        </div>
                        <div className="contactbutton">
                            <Link to="/Contact" className="navbar-link" onClick={() => onUpdateActiveLink('Contact')}>
                                <button>
                                    <span>Let's Connect</span>
                                </button>
                            </Link>
                        </div>
                    </span>
                </Navbar.Collapse>
                <Navbar.Toggle aria-controls="basic-navbar-nav" >
                    <span className='navbar-toggler-icon'></span>
                </Navbar.Toggle>
            </Container>
        </Navbar>
    )
}
